.intro {
  background-color: #333333;
  color: white;
  text-transform: uppercase;
  height: 5vh;
  line-height: 5vh;
}

.about-container {
  display: -ms-flexbox;
  display: flex;
  padding: 15vh;
  -ms-flex-pack: center;
      justify-content: center;

}

.about-container img {
  width:40vh; 
  height: auto;
  padding: 2vh;
}

@media all and (max-width: 800px) {
  .about-container {
    -ms-flex-pack: column;
        justify-content: column;
    padding: 25px;
    text-align: center;
  }
}

@media all and (max-width: 500px) {
  .about-container {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
  }
}






